
<template>
    <div class="stepsdemo-content">
        <Card>
           <template v-slot:title>
             <div class="row mt-2">
                            <div class="col-12 app-card app-card-orders-table shadow-sm mb-5 mt-3">
                                <div class="app-card-body">
                                    <div class="table-responsive">
                                        <table class="table app-table-hover text-center">
                                            <thead>
                                                <tr>
                                                    <th class="cell">Descripción</th>
                                                    <th class="cell">Unidad</th>
                                                    <th class="cell">Cantidad</th>
                                                    <th class="cell">Precio Unitario (Sin IGV)</th>
                                                    <th class="cell">Precio Total (Sin IGV)</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td class="cell">DESCRIPCION 1</td>
                                                    <td class="cell">UND</td>
                                                    <td class="cell">1</td>
                                                    <td class="cell">2,900.00</td>
                                                    <td class="cell">2,900.00</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
            </template>
            <template v-slot:footer>
                <div class="grid grid-nogutter justify-content-between">
                    <Button label="Back" @click="prevPage()" icon="pi pi-angle-left" />
                    <Button label="Next" @click="nextPage()" icon="pi pi-angle-right" iconPos="right" />
                </div>
            </template>
        </Card>
    </div>
</template>

<script>
export default {
    data () {
        return {
            selectedClass: '',
            classes: [
                {name: 'First Class', code: 'A', factor: 1},
                {name: 'Second Class', code: 'B', factor: 2},
                {name: 'Third Class', code: 'C', factor: 3}
            ],
            vagons: [],
            selectedVagon: '',
            seats: [],
            selectedSeat: ''
        }
    },
    methods: {
        nextPage() {
            this.$emit('nextPage', {formData: {class: this.selectedClass.name, vagon: this.selectedVagon.vagon, seat: this.selectedSeat.seat}, pageIndex: 2});
        },
        prevPage() {
            this.$emit('prevPage', {pageIndex: 2});
        }
    }
}
</script>
